import { Suspense } from 'react';
import { Canvas} from '@react-three/fiber';
import { useGLTF, OrbitControls, Preload, Environment, Stats } from '@react-three/drei'
import CanvasLoader from './CanvasLoader';

 function App() {

  return (
    <div className='w-full h-full canvas-div'>
    <Canvas 
        gl={{ antialias: false, logarithmicDepthBuffer: true }}
        frameloop='demand'
        shadows 
        camera={{ position: [-15, 0, 10], fov: 25 }}>
        <Suspense 
          fallback={<CanvasLoader />}
        > 
          <Environment preset="city" background blur={1} />
          <Scene />
          <OrbitControls/>
          <Preload all />
        </Suspense> 
        </Canvas>
    </div>
  )
}

function Scene() {
  const { scene } = useGLTF(`${process.env.PUBLIC_URL}/models/3.glb`)
  return (<primitive
        receiveShadow
        object={scene}
        scale={0.04} 
        position={[0, -3, 0]}
        rotation={[0, 1.2, 0]}
    />
  )
}

useGLTF.preload(`${process.env.PUBLIC_URL}/models/3.glb`);

export default App
